styles; set buttons to display block; set base (pre UA stylesheet) font size; */
*, *::before, *::after { box-sizing: border-box; }
* { margin: 0; }
input, button, textarea, select { font: inherit; }
button { display: block; cursor: pointer; }
:root{ font-size: 16px; }  /*chrome default*/
/* imported google fonts examples */
/* .roboto { font-family: "Roboto"; font-weight: 400,500; font-style: normal; } */
/* variables---------------------------------------------------------------- */
:root{
  --black: hsl(0, 0%, 0%);
  --white: hsl(0, 0%, 100%);
  /* tailwind palette based, then hue shifted */
  /*blue*//*
  --bg950: hsl(199, 91%, 9%);
  --bg900: hsl(197, 86%, 16%);
  --bg800: hsl(196, 88%, 20%);
  --bg700: hsl(196, 94%, 24%);
  --bg600: hsl(194, 94%, 30%);
  --mg500: hsl(193, 84%, 39%);
  --fg400: hsl(191, 64%, 52%);
  --fg300: hsl(189, 72%, 67%);
  --fg200: hsl(185, 76%, 80%);
  --fg100: hsl(182, 80%, 90%);
  --fg50:  hsl(185, 81%, 96%);
  */
  /*brown (for dev)*/
  --bg950: hsl(33, 91%, 9%);
  --bg900: hsl(31, 86%, 16%);
  --bg800: hsl(30, 88%, 20%);
  --bg700: hsl(30, 94%, 24%);
  --bg600: hsl(28, 94%, 30%);
  --mg500: hsl(27, 84%, 39%);
  --fg400: hsl(25, 64%, 52%);
  --fg300: hsl(23, 72%, 67%);
  --fg200: hsl(19, 76%, 80%);
  --fg100: hsl(16, 80%, 90%);
  --fg50:  hsl(19, 81%, 96%);

}

/* main CSS */
body {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-weight: 400;
  background-color: var(--bg900);
}
.projectDiv {
  margin: 1rem 0;
  padding: 0.25rem 1rem;
  background-color: var(--fg200);
  input {
    width: 100%;
    font-size: 1.25rem;
    background-color: var(--fg50);
    cursor: text; /* for datetime-local */
    margin: 0.25rem 0;
    border-style: none;
  }
  input::placeholder {
    color: var(--fg200);
  }
  .projectBtnsWrap{
    display: flex;
    justify-content: space-between;
    gap: 0.25rem;
    button {
      background-color: var(--fg200);
      color: var(--bg900);
      border-radius: 5px;
      border-style: none;
      padding: 0.25rem;
      margin: 0.1rem 0 0.5rem 0;
      box-shadow: 0 0 2px 2px var(--bg600);
    }
    button:hover{
      background-color: var(--fg50);
    }
  }
}
.todoDiv {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: var(--fg100);
  margin: 0 0 0.5rem 0;
  gap: 0 0.25rem;
  button {
    border-style: none;
    background-color: var(--fg300);
  }
  .todoTitle {
    flex-basis: 75%;
  }
  .todoNotes {
    flex-basis: 75%;
    margin: 0 auto 0.25rem;
    display: block;
  }
  .completionBox {
    width: 2rem;
    height: 2rem;
    accent-color: var(--fg400);
  }
  .dueDateTime {
    flex-basis: 42%;
  }
  .prioritySelect {
    width: 4.75rem;
    cursor: pointer;
  }
  .noDisplay {
    display: none;
  }
}
